import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import Main from '../views/main.vue'
import ShowShi from '../views/shoushi.vue'
import One from '../views/one.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    redirect: '/home/main',
    children: [
      {
        path: '/home/main',
        name: 'main',
        component: Main
      },
      {
        path: '/home/shoushi',
        name: 'main',
        component: ShowShi
      },
      {
        path: '/home/one',
        name: 'main',
        component: One
      },
    ]
  },
]

const router = new VueRouter({
  routes
})

export default router
